@import url(https://cors-anywhere.herokuapp.com/https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://cors-anywhere.herokuapp.com/https://fonts.googleapis.com/css?family=Open+Sans|Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --black: #2a2438;
  --lightblack: #352f44;
  --violet: #310a5d;
  --lightviolet: #411e8f;
  --red: #d72323;
  --white: #eeeeee;
  --green: #a7d129;
  --google-blue: #4285f4;
  --button-active-blue: #1669F2;

}

.error404Container{
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.alertMessage{
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: rgba(0,0,0,1);
  padding: 10px;
}

.google-btn {
  display: flex;
  align-items: center;
  height: 40px;
  background-color: #4285f4;
  background-color: var(--google-blue);
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
}
.google-btn .google-icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin-right: 3px;
  border-radius: 2px;
  background-color: #fff;
}
.google-btn .google-icon {
    width: 20px;
    height: 20px;
}
.google-btn .btn-text {
  margin-right: 3px;
}
.google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
    box-shadow: 0 0 6px var(--google-blue);
}
.google-btn:active {
    background: #1669F2;
    background: var(--button-active-blue);
}

.material-icons{
  color: #eeeeee;
  color: var(--white);
  font-size: 2.2em;
}

.text-icons{
  background-color: #eeeeee;
  background-color: var(--white);
  color: #2a2438;
  color: var(--black);
  font-size: small;
  padding: 4px 10px;
  margin-left: 5px;
  border-radius: 1024px;
}

.deleteIcon{
  color: #d72323;
  color: var(--red);
}
.checkIcon{
  color: #a7d129;
  color: var(--green);
}

.loadingIconContainer{
  position: fixed;
  top: 50%;
  left: 50%;
}

.loadingIcon{
  -webkit-animation: rotation 1s infinite linear;
          animation: rotation 1s infinite linear;
}

@-webkit-keyframes rotation {
  from {-webkit-transform: rotate(0deg);}
  to   {-webkit-transform: rotate(359deg);}
}

@keyframes rotation {
  from {-webkit-transform: rotate(0deg);}
  to   {-webkit-transform: rotate(359deg);}
}

.appInputs{
  border: 0px;
  border-bottom: 1px solid #eeeeee;
  border-bottom: 1px solid var(--white);
  background-color: #2a2438;
  background-color: var(--black);
  color: #eeeeee;
  color: var(--white);
  padding: 5px;
  font-size: x-large;
  /* flex-grow: 1; */
}

.notification{
  padding: 10px;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  background-color: rgba(49, 49, 49, 1);
  /* background-color: var(--red); */
  text-transform: uppercase;
  position: fixed;
  top: 10px;
  right: 30px;
  left: 30px;
  text-align: center;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type=number] {
    -moz-appearance:textfield; /* Firefox */
}

input:invalid {
  box-shadow:none;
}

body{
  font-family: 'Open Sans', sans-serif;
  background-color: #352f44;
  background-color: var(--lightblack);
  color: white;
  /* font-family: 'Source Sans Pro', sans-serif; */
}

.titlebar{
  font-family: 'Source Sans Pro', sans-serif;
  text-transform: capitalize;
  background-color: #2a2438;
  background-color: var(--black);
  color: white;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  padding: 10px;
  font-weight: bolder;
}

.addnewIconContainer{
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.addnewIcon{
  font-size: 4rem;
  color: #eeeeee;
  color: var(--white);
}

.detailedBill{
  margin: 5px 10px;
}
.detailedBill .receiptView{
  box-sizing: border-box;
  padding: 5px 15px;
  width: inherit;
  background-color: #2a2438;
  background-color: var(--black);
  border-top: 1px dashed #eeeeee;
  border-top: 1px dashed var(--white);
  /* display: none; */
}

.billBrief{
  display: flex;
  padding: 0px 5px;
  background-color: #2a2438;
  background-color: var(--black);
  /* border-bottom: 1px solid var(--white); */
}

.billBriefControls{
  display: flex;
  flex-direction: column;
  padding: 10px;
  padding-right: 5px;
  /* border-right: 1px solid var(--lightblack); */
}

.billBriefDetails{
  padding: 10px;
  padding-right: 5px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.billDateAndName{
  display: flex;
  align-items: baseline;
}

.billName{
  flex-grow: 1;
  font-size: x-large;
  font-weight: bold;
  text-transform: capitalize;
}

.billDate{
  font-size: small;
}

.billMember{
  margin-right: 2px;
  background-color: #352f44;
  background-color: var(--lightblack);
  color: white;
  font-size: 0.8em;
  padding: 1px 7px;
  border-radius: 1024px;
}

.newBillForm{
  margin: 5px 10px;
  display: flex;
  flex-direction: column;
  padding: 15px;
  background-color: #2a2438;
  background-color: var(--black);
}

.newMemberNames{
  /* border: 2px solid black; */
  margin: 10px 0px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.newBillName{
  margin: 10px 0px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.newMembers{
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.newNameChip{
  display: flex;
  align-items: center;
  font-size: larger;
  text-transform: capitalize;
  padding: 5px 10px;
  margin: 3px;
  border-radius: 1024px;
  background-color: #352f44;
  background-color: var(--lightblack);
}


.chipControlIcon{
  font-size: larger;
  margin-left: 5px;
  color: #d72323;
  color: var(--red);
}

.billItemForm, .splitForm, .memberWiseReceipt{
  margin: 5px 10px;
  display: flex;
  flex-direction: column;
  padding: 15px;
  background-color: #2a2438;
  background-color: var(--black);
}

.billItemForm .appInputs{
  font-size: larger;
}

.splitForm .appInputs{
  font-size: medium;
}


.splitMembers{
  display: flex;
  font-size: large;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
}


.itemSummary{
  display: flex;

}

.nameChipContainer{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.nameChip{  
  display: flex;
  align-items: center;
  font-size: small;
  padding: 5px 10px;
  margin: 3px;
  border-radius: 1024px;
  background-color: #352f44;
  background-color: var(--lightblack);
  text-transform: capitalize;
}


.memberReceiptView{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 5px;
}

.memberReceiptView div{
  flex-grow: 1;
  margin: 2px 3px;
}

.memberReceiptView table{
  width: 100%;
  border-collapse: collapse;
}

.dueStatus{
  display: flex;
  flex-direction: row-reverse;
}




@-webkit-keyframes shake{
  0%, 100% { left: 0px;}
  20% , 60%{left: 15px;}
  40% , 80%{left: -15px;}

}




@keyframes shake{
  0%, 100% { left: 0px;}
  20% , 60%{left: 15px;}
  40% , 80%{left: -15px;}

}
